import React, {FC} from "react"
import scss from './Checkbox.module.scss'

interface ICheckbox {
    label: string
    onChange: (state:boolean) => void
}

const Checkbox:FC<ICheckbox> = ({label, onChange}):React.ReactElement => {
    return <div className={scss['Checkbox']}>
        <label className={scss['Checkbox__label']}>
            <input onChange={(e:any) => onChange(e.target.checked)} className={scss['Checkbox__input']} type="checkbox"/>
           <span className={scss['Checkbox__box']}>
<svg width="17" height="12" viewBox="0 0 17 12" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M1.71429 5.79504L6.29389 10.7143L15.0714 1.28572" stroke="#3B75A7" strokeWidth="2.35714" strokeLinecap="round" strokeLinejoin="round"/>
</svg>
           </span>
            <span className={scss['Checkbox__text']} dangerouslySetInnerHTML={{__html: label}}/>
        </label>
    </div>
}

export default Checkbox
