import React, {FC, ReactElement, useState} from "react";

import scss from './Agree.module.scss'
import classNames from "classnames/bind";
const cn = classNames.bind(scss)

import Back from "../Back/Back";
import Checkbox from "../Checkbox";
import Btn from "../Btn/Btn";
import {useNavigate} from "react-router";

type Agree = {

}

import img from '../../assets/images/image.png'

const Agree:FC<Agree> = ({}):ReactElement => {
   const navigate = useNavigate()
   const [disabled, setDisabled] = useState<boolean>(true)

   const onClick = () => {
      navigate('/mnemonic')
   }

   return <div className={cn('Agree')}>
      <Back />

      <div className={cn('Agree__main')}>
         <img className={cn('Agree__main-logo')} src={img} alt=""/>

         <h1>You Are Connected To The Toncoin Network</h1>

         <span>Rather, Log In to your <b>TON <br/> Project - Top Keeper</b> account and start working with the TON token</span>

         <Checkbox label={`I agree to <a href="http://toproj.org/privacy-policy.html">Terms Of Use</a>`} onChange={(state) => setDisabled(!state)} />
      </div>

      <Btn fontSize={18} label={`Go To <br> Authorization`} disabled={disabled} onClick={onClick}/>
   </div>
}

export {Agree}

