                           <path d="M4.7531 25.3207C7.37816 25.3207 9.5062 23.2445 9.5062 20.6834C9.5062 18.1224 7.37816 16.0462 4.7531 16.0462C2.12803 16.0462 0 18.1224 0 20.6834C0 23.2445 2.12803 25.3207 4.7531 25.3207Z" fill="white" fill-opacity="0.8"/>
