import React, {FC, useEffect, useRef, useState} from "react";

import scss from './Input.module.scss'
import classNames from "classnames/bind";

const cn = classNames.bind(scss)

import {Close, Open} from "../Svg/Password";

type Input = {
   label?: string
   placeholder?: string
   type?: 'password' | 'text' | 'textarea'
   height?: number
   onChange?: (state: string) => void
   error?: boolean
   changeValue?: string
   icon?: React.ReactElement
}

const Input: FC<Input> = ({height = 52, type = 'text', label, onChange, placeholder, error, changeValue, icon}) => {
   const [handleType, setHandleType] = useState<Input['type']>(type)
   const [active, setActive] = useState<boolean>(false)
   const [state, setState] = useState<string>('')
   const [focus, setFocus] = useState<boolean>(false)
   const textarea = useRef<HTMLTextAreaElement>(null)

   const onChangeHandler = (val: string) => {
      setState(val)
      onChange && onChange(val)
   }

   const onClick = () => {
      if (!active) {
         setActive(true)
         textarea.current?.focus()
      }
   }

   const onFocus = () => {
      setFocus(true)
   }

   const onBlur = () => {
      setFocus(false)
   }

   useEffect(() => {
      setState(changeValue ?? '')
   }, [changeValue])

   return <div className={cn('Input')}>
      {label ? <div className={cn('Input__label', {focus: focus || state.length})}>{label} </div> : null}

      {type !== 'textarea'
         ? <div className={cn('Input__wrapper')}>
            <input style={{height}} onChange={(e: any) => onChangeHandler(e.target.value)}
                   className={cn('Input__wrapper-body', {error, icon})} value={state}
                   type={handleType} placeholder={placeholder}/>

            {type === 'password'
               ? <div onClick={() => setHandleType(handleType === 'text' ? 'password' : 'text')}
                      className={cn('Input__wrapper-eye')}>{handleType === 'text' ? <Close/> : <Open/>}</div>
               : null}
         </div>
         : <div onClick={onClick}>
            <textarea style={{height}} ref={textarea} className={cn('Input__textarea', {error, focus: focus || state.length})}
                      placeholder={placeholder} onFocus={onFocus} onBlur={onBlur} value={state}
                      onChange={(e: any) => onChangeHandler(e.target.value)}/>
         </div>}
   </div>
}

export default Input
