import scss from './Btn.module.scss'
import classNames from "classnames/bind";
const cn = classNames.bind(scss)

import React, {FC} from "react";

type Btn = {
   label: string
   disabled?: boolean
   onClick?: () => void
   fontSize?: number
}

const Btn:FC<Btn> = ({disabled, label, onClick, fontSize = 24}) => {
   const onClickHandler = () => {
      if (disabled) return
      onClick && onClick()
   }

   return <div onClick={onClickHandler} className={cn('Btn', {disabled})}>
      <span style={{fontSize}} dangerouslySetInnerHTML={{__html: label}}/>
      <div className={cn('Btn__icon')}>
         <svg width="21" height="16" viewBox="0 0 21 16" fill="#3B75A7" xmlns="http://www.w3.org/2000/svg">
            <path d="M20.7071 8.70711C21.0976 8.31658 21.0976 7.68342 20.7071 7.29289L14.3431 0.928932C13.9526 0.538408 13.3195 0.538408 12.9289 0.928932C12.5384 1.31946 12.5384 1.95262 12.9289 2.34315L18.5858 8L12.9289 13.6569C12.5384 14.0474 12.5384 14.6805 12.9289 15.0711C13.3195 15.4616 13.9526 15.4616 14.3431 15.0711L20.7071 8.70711ZM0 9H20V7H0V9Z"/>
         </svg>

         <svg className={cn('Btn__icon-bg')} width="71" height="83" viewBox="0 0 71 83" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M70.4727 12.6057C64.9187 13.7908 63.94 14.7512 62.7364 20.2114C61.5309 14.7512 60.554 13.7891 55 12.6057C60.554 11.4206 61.5327 10.4602 62.7364 5C63.94 10.4602 64.9187 11.4224 70.4727 12.6057Z" fill="#3B75A7"/>
            <path d="M55.4727 74.6057C49.9187 75.7908 48.94 76.7512 47.7364 82.2114C46.5309 76.7512 45.554 75.7891 40 74.6057C45.554 73.4206 46.5327 72.4602 47.7364 67C48.94 72.4602 49.9187 73.4224 55.4727 74.6057Z" fill="#3B75A7"/>
            <path d="M20 10C12.8209 11.5582 11.5559 12.8209 10 20C8.44183 12.8209 7.17913 11.5559 0 10C7.17913 8.44183 8.44414 7.17913 10 0C11.5559 7.17913 12.8209 8.44414 20 10Z" fill="#3B75A7"/>
         </svg>
      </div>
   </div>
}

export default Btn
